export default "html, body {\r\n    margin: 0;\r\n    overflow: hidden;\r\n}\r\n\r\npre {\r\n    overflow: auto;\r\n    max-height: 90%;\r\n    width: 100%;\r\n    border-radius: 1px;\r\n    white-space: pre-wrap;\r\n}\r\n\r\n#content {\r\n    position: absolute;\r\n    top: 0; right: 0; left: 0; bottom: 20px;\r\n}\r\n\r\na:after {\r\n    content: '';\r\n    padding-right: 5px;\r\n    border-right-width: 1px;\r\n    border-right-style: solid;\r\n    border-color: inherit;\r\n}\r\n\r\na:last-of-type:after {\r\n    border: none;\r\n}\r\n\r\n@media (max-width: 420px) {\r\n    html, body  {\r\n      font-size: 8px;\r\n      height: 100%;\r\n    }\r\n}\r\n\r\n@media (min-width: 420px) {\r\n    html, body {\r\n      font-size: 15px;\r\n      height: 100vh;\r\n    }\r\n}\r\n\r\n/* Markdown Styling */\r\n#work{\r\n    max-height: 50%;\r\n}\r\n\r\nh1, h2{\r\n    margin-top: 0;\r\n    margin-bottom: 0;\r\n    \r\n}\r\nh1, h2{\r\n    width: fit-content;\r\n    border-bottom: 1px solid #ccc;\r\n}\r\n\r\nblockquote{\r\n    margin: 0px 0px 0px 2rem;\r\n}\r\n\r\np{\r\n    margin-top: 0px;\r\n}\r\n\r\n/* Contact me form */\r\n#contact-me{\r\n    display: none;\r\n    opacity: 0;\r\n    width: 0px;\r\n    height: 0px;\r\n}\r\n\r\nform {\r\n    display: flex;\r\n    flex-direction: column;\r\n    font-family: Arial, sans-serif;\r\n}\r\n\r\nlabel {\r\n    display: block;\r\n    font-weight: bold;\r\n    margin-top: 10px;\r\n    margin-bottom: 5px;\r\n\r\n}\r\n\r\ninput[type=\"text\"],\r\ninput[type=\"email\"],\r\ntextarea {\r\n    width: 100%;\r\n    padding: 0.5rem;\r\n    border-radius: 4px;\r\n    box-sizing: border-box;\r\n    font-size: 1rem;\r\n    background-color: rgb(82, 81, 81);\r\n    color: aliceblue;\r\n}\r\n\r\ntextarea {\r\n    height: 9rem;\r\n}\r\n\r\nbutton[type=\"submit\"] {\r\n    background-color: rgb(75, 72, 72);\r\n    margin: 2rem 0 0 0;\r\n    color: white;\r\n    padding: 0.5rem;\r\n    border: none;\r\n    border-radius: 12px;\r\n    cursor: pointer;\r\n    